.App {
  display: flex;
  justify-content: center;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.profile-image {
  width: 300px; /* Adjust size as needed */
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 30px;
}

.profile-text h1 {
  font-size: 14px;
  letter-spacing: 2px;
  color: #888;
  margin-bottom: 5px;
}

.profile-text h2 {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  margin: 5px 0;
}

.profile-text p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
  max-width: 600px;
}


.App-link {
  color: #1a1f20;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
